import { usersAPI } from "../api/api";
import { updateObjectArray } from "../utils/object-helpers";

const FOLLOW = 'FOLLOW';
const UNFOLLOW = 'UNFOLLOW';
const SET_USERS = 'SET-USERS'
const SET_USERS_TOTAL_COUNT = 'SET-USERS-TOTAL-COUNT'
const SET_CURRENT_PAGE = 'SET-CURRENT-PAGE'
const TOGGLE_IS_FETCHING = 'TOGGLE-IS-FETCHING'
const FOLLOWING_IN_PROGRESS = 'TOGGLFOLLOWING_IN_PROGRESS'
let initialState = {
    users: [],
    pageSize: 20,
    currentPage: 1,
    totalUsersCount: 0,
    isFatching: false,
    followingInProgress: []
};

const usersReducer = (state = initialState, action) => {

    switch (action.type) {

        case FOLLOW:
            return {
                ...state,
                users: updateObjectArray(state.users, action.userId, "id", { followed: true })
                // users: [...state.users]
                // users: state.users.map(u => {
                //     if (u.id === action.userId) {
                //         return { ...u, followed: true }
                //     }
                //     return u
                // })
            }
        case UNFOLLOW:
            return {
                ...state,
                users: updateObjectArray(state.users, action.userId, "id", { followed: false })
                // users: state.users.map(u => {
                //     if (u.id === action.userId) {
                //         console.log(u.followed);
                //         return { ...u, followed: false }
                //     }
                //     return u
                // })
            }
        case SET_USERS:
            return {
                ...state,
                users: action.users
            }
        case SET_USERS_TOTAL_COUNT:
            return {
                ...state,
                totalUsersCount: action.totalCount
            }
        case SET_CURRENT_PAGE:
            return {
                ...state,
                currentPage: action.pageNumber
            }
        case TOGGLE_IS_FETCHING:
            return {
                ...state,
                isFatching: action.isFetching
            }
        case FOLLOWING_IN_PROGRESS:
            return {
                ...state,
                followingInProgress: action.isFatching
                    ? [...state.followingInProgress, action.userId]
                    : state.followingInProgress.filter(id => id != action.userId)
            }

        default:
            return state;
    }
}

//ActionCreators
export const followSuccess = (user) => ({ type: FOLLOW, userId: user })
export const unfollowSuccess = (user) => ({ type: UNFOLLOW, userId: user })
export const setUsers = (users) => ({ type: SET_USERS, users })
export const setUsersTotalCount = (totalCount) => ({ type: SET_USERS_TOTAL_COUNT, totalCount })
export const setCurrentPage = (pageNumber) => ({ type: SET_CURRENT_PAGE, pageNumber })
export const setToggleIsFetching = (isFetching) => ({ type: TOGGLE_IS_FETCHING, isFetching })
export const setFollowingInProgress = (isFatching, userId) => ({ type: FOLLOWING_IN_PROGRESS, isFatching, userId })

//ThunkCreators
export const requestUsers = (page, pageSize) => async (dispatch) => {
    dispatch(setToggleIsFetching(true))
    dispatch(setCurrentPage(page))

    let data = await usersAPI.getUsers(page, pageSize).
        then(data => {
            dispatch(setToggleIsFetching(false))
            dispatch(setUsers(data.items));
            dispatch(setUsersTotalCount(data.totalCount));
        })
}
export const follow = (userId) => async (dispatch) => {
    followUnfollowFlow(dispatch, userId, usersAPI.follow.bind(userId), followSuccess)
}
export const unfollow = (userId) => async (dispatch) => {
    followUnfollowFlow(dispatch, userId, usersAPI.unfollow.bind(userId), unfollowSuccess)
}

//function
const followUnfollowFlow = async (dispatch, userId, apiMethod, actionCreator) => {

    dispatch(setFollowingInProgress(true, userId))
    let resultCode = await apiMethod(userId)
    if (resultCode == 0) {
        dispatch(actionCreator(userId))
    }
    dispatch(setFollowingInProgress(false, userId))
}

export default usersReducer;